export default {
  save(val,hint="保存中..."){
    val.submitClass = "btn-default";
    val.submitValue = hint;
    val.submitDisabled = true;
  },
  success(val,hint="保存成功",callback=()=>{}){
    val.submitClass = "btn-success";
    val.submitValue = hint;
    val.submitDisabled = true;
  },
  error(val,hint="保存失败",callback=()=>{}){
    val.submitClass = "btn-danger";
    val.submitValue = hint;
    val.submitDisabled = true;
  },
}


