import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
//用户管理
import userList from '@/components/user/list'
import userNew from '@/components/user/new'
import userEdit from '@/components/user/edit'
//权限管理
import permission from '@/components/permission/list'
//设备管理
import devices from '@/components/devices/list'

Vue.use(Router)

export default new Router({
  // mode : "history",
  routes: [
    {
      path: '/',
      redirect : "/user"
    }, 
    //用户管理
    {
      path: '/user',
      name: 'user',
      component: userList
    },
    {
      path: '/user/new',
      name: 'userNew',
      component: userNew
    },
    // {
    //   path: '/user/edit/:id/:user/:phone/:email/:userType/:devices',
    //   name: 'userEdit',
    //   component: userEdit
    // },
    {
      path: '/user/edit/:id/:user/:phone/:email/:userType/:devices',
      name: 'userEdit',
      component: userEdit
    },
    //权限管理
    {
      path: '/permission',
      name: 'permission',
      component: permission
    },
    //设备管理
    {
      path: '/devices',
      name: 'devices',
      component: devices
    }
  ]
})



